@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@700&family=Source+Sans+Pro:wght@400;600&display=swap');


.hosplogin {
    background-image: url("../hospital/images/new.png");
    /* background-color: rgb(216, 211, 195); */
    background-repeat: repeat;
    background-size: contain;
    height: 721px;
}
  
.hospImg {
    top: 133px;
    width: 400px;
    height: 370px;
    position: relative;
    right: 80px;
    object-fit: contain;
}
  
.logo {
    width: 180px;
    height: 100px;
    object-fit: contain;
}
  
.hosp_login_form_container {
    font-family: 'Source Sans Pro', sans-serif;
    color: rgb(97, 51, 81);
    min-width: 300px;
    
    height: 450px;
    padding: 20px;
    position: absolute;
    right: 740px;
    top: 15%;
    background-color: rgb(242, 242, 242);
    border-radius: 20px;
}
  
.hosp_login_form{
    font-family: 'Source Sans Pro', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}
  
.hosp_login_form > div > input {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 17px;
    width: 260px;
    height: 43px;
    padding: 10px;
    border-radius: 4px;
    border: transparent;
}
  
.hosp_login_button {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 20px;
    font-weight: bold;
    min-width: 100%;
    padding: 8px;
    border-radius: 8px;
    border: transparent;
    color: white;
    background-color: rgb(168, 157, 120);
    cursor: pointer;
}

.hosp_login_button:hover{
    background-color:rgb(169, 154, 105) ;
}
  
.hosp_login_heading {
    color: rgb(238, 236, 230);
    font-family: 'Merriweather', serif;
    font-size: 35px;
    position: absolute;
    bottom: 385px;
    left: 100px;
}
  
.hide {
    display: none;
}
  
.err {
    margin: 15px;
    background-color: rgb(201, 98, 98);
    color: black;
    border: 1px solid red;
}
  
.hospPortImg {
    position: absolute;
    right: 0;
    top: -20px;
    width: 600px;
    height: 500px;
    object-fit: contain;
}

  